import React from "react";
import will from "./photo.png";
import brad from "./brad.png";
import bruce from "./v.png";
import jen from "./ja.png";
import lop from "./lop.png";
import kira from "./kira.png";
import megan from "./megan.png";
import mo from "./mo.png";
import jack from "./jack.png";
import depp from "./depp.png";
import la from "./la.png";
import man from "./man.png";
import olivia from "./olivia.png";
import girl from "./gir.png";
import man1 from "./man1.png";
import './index.sass';

const Photos = () => {
  return (
    <main className="photos">
      <div>
        <h2>Portfolio</h2>
        <h3>Gallery One</h3>
      </div>
      <div>
        <img src={will} alt="will" />
      </div>
      <div>
        <img src={brad} alt="brad" />
      </div>
      <div>
        <img src={bruce} alt="bruce" />
      </div>
      <div>
        <img src={jen} alt="jen" />
      </div>
      <div>
        <img src={lop} alt="lop" />
      </div>
      <div>
        <img src={kira} alt="kira" />
      </div>
      <div>
        <img src={megan} alt="megan" />
      </div>
      <div>
        <img src={mo} alt="mo" />
      </div>
      <div>
        <img src={jack} alt="jack" />
      </div>
      <div>
        <img src={depp} alt="depp" />
      </div>
      <div>
        <img src={la} alt="la" />
      </div>
      <div>
        <img src={man} alt="man" />
      </div>
      <div>
        <img src={olivia} alt="olivia" />
      </div>
      <div>
        <img src={girl} alt="girl" />
      </div>
      <div>
        <img src={man1} alt="man1" />
      </div>
    </main>
  );
};

export default Photos;
