import React from "react";
import logo from "./square_logo.png";
import "./index.sass";

const Header = () => {
  return <header>
      <div className="menu">
        <i className="far fa-compass" />
      </div>
      <div className="logo">
        <img src={logo} alt="logo" />
      </div>
      <div className="socials">
        <a href="https://www.facebook.com/" target="_blank" rel="noreferrer noopener">
          <i className="fab fa-facebook-f" />
        </a>
        <a href="https://twitter.com/" target="_blank" rel="noreferrer noopener">
          <i className="fab fa-twitter" />
        </a>
        <a href="https://www.instagram.com/" target="_blank" rel="noreferrer noopener">
          <i className="fab fa-instagram" />
        </a>
        <a href="https://www.tumblr.com/" target="_blank" rel="noreferrer noopener">
          <i className="fab fa-tumblr" />
        </a>
        <a href="https://plus.google.com/discover" target="_blank" rel="noreferrer noopener">
          <i className="fab fa-google-plus-g" />
        </a>
        <a href="https://www.google.com/intl/en/gmail/about/#" target="_blank" rel="noreferrer noopener">
          <i className="fas fa-envelope" />
        </a>
      </div>
    </header>;
};

export default Header;
