import React, { Component } from 'react';
import Header from './components/Header';
import Photos from './components/Photos';
import Footer from './components/Footer';
import './App.sass';

class App extends Component {
  render(){
    return (
      <div className="App">
        <Header />
        <Photos />
        <Footer />
      </div>
      
    )
  }
}
  

export default App;